.card {
   max-width: 700px;
   display: grid;
   grid-template-columns: minmax(90px, 10%) 1fr auto;
   align-items: center;
   gap: 10px;

   &__image {
      width: 80px;
      height: 80px;
      border-radius: 50%;

   }

   &__button {
      box-sizing: border-box;

      font-size: 18px;
      font-weight: 600;
      color: rgb(56, 56, 56);
      background: transparent;
      border: 3px solid rgb(31, 31, 31);
      height: 40px;
      border-radius: 5px;

   }

   &__button:focus {
      outline: 3px solid rgb(168, 168, 168);
      border-radius: 5px;
   }
}





.users__wrapper {
   padding: 0;

   display: flex;
   flex-direction: column;
   gap: 30px;

}