@import './../../styles/constant.scss';

.overlay {
   background: rgba(0, 0, 0, 0.534);
   z-index: 10;
   width: 100vw;
   height: 100vh;
   position: absolute;
   top: 0;
   right: 0;
   left: 0;
   bottom: 0;
}

.navigation__wrapper {
   position: fixed;
   top: 60px;
   left: 0;
   background: rgb(31, 31, 31);
   height: 100vh;
   width: $navWidth;
   transition: .4s ease-in-out;
   z-index: 30;
}

@media screen and (max-width:768px) {
   .navigation__wrapper {
      width: 60px;
   }
}

@media screen and (max-width:414px) {
   .navigation__wrapper {
      left: -60px;
   }
}

.navigation__wrapper_show {
   width: $navWidth;
   left: 0;
}

@media screen and (max-width:414px) {
   .navigation__wrapper_show {
      width: 100%
   }
}

.navigation__nav {
   display: flex;
   flex-direction: column;
   overflow: hidden;
}

.navigation__item {
   font-size: 26px;
   list-style-type: none;
}

a {
   text-decoration: none;
   color: rgb(189, 189, 189);

}

a:focus {
   outline: none;
}

.navigation__container {
   width: 120px;
   display: grid;
   grid-template-columns: auto auto;
   place-items: center;

   column-gap: 50px;
   padding-bottom: 6px;
   padding-left: 16px;
   padding-top: 17px;
}

.navigation__active {
   color: rgb(17, 109, 133)
}

.navigation__image {
   width: 25px;
   height: 25px;
}