.header__wrapper {
   position: fixed;
   height: 60px;
   background: rgb(175, 168, 87);
   display: grid;
   width: 100%;
   grid-template-columns: auto 1fr 60px;
   justify-content: space-around;
   align-items: center;
   top: 0;
   left: 0;
   padding-left: 20px;
   padding-right: 20px;
   transition: ease .4s;
   z-index: 2;
   -webkit-box-shadow: 12px 12px 30px 0px rgba(50, 50, 50, 0.32);
   -moz-box-shadow: 12px 12px 30px 0px rgba(50, 50, 50, 0.32);
   box-shadow: 12px 12px 30px 0px rgba(50, 50, 50, 0.32);

}

.header__image {
   width: 30px;
   height: 30px;
}

.header__button {
   width: 30px;
   padding: 0;
   display: flex;
   place-items: center;
   height: 20px;
   background: transparent;
   border: none;
   transition: ease .2s;
}

@media screen and (min-width:769px) {
   .header__wrapper {
      grid-template-columns: 1fr 60px;
   }

   .header__button {
      display: none;
   }
}

.header__button:focus {
   outline: none;
}

.header__title {
   color: rgb(36, 36, 36);
   font-size: 26px;
   margin: 0;
   justify-self: center;
}

.header__login {
   width: 30px;
   height: 30px;
   border-radius: 50%;
   background: rgb(212, 212, 212);
}