@import './styles/constant.scss';

.app__wrapper {
  margin-top: 60px;
  padding: 5px;
  font-family: 'Roboto', sans-serif;
  position: relative;


}



.mainWrapper {
  margin: 0;
  margin-left: $navWidth;
  transition: ease .4s;

}


@media screen and (max-width:768px) {
  .mainWrapper {
    margin-left: 60px;
  }

}

.mainWrapper_show {
  transition: .4s ease;
}

@media screen and (max-width:414px) {
  .mainWrapper {
    margin: 0 auto;
  }

}

.app__overley {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(22, 22, 22, 0.856);
  overflow-y: hidden;

}